*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
.app{
    min-width: 100vw;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.calc{
    min-width: 30%;
    min-height: 70%;
    display: grid;
    grid-template-columns: auto auto auto auto;
    grid-template-rows: auto auto auto auto auto auto;
}
.i1{
    grid-area: 1 / 1 / 1 / 5;
    height: 5rem;
}
.i2{
    grid-area: 2 / 1 / 2 / 1;
    height: 5rem;
    cursor: pointer;
}
.i3{
    grid-area: 2 / 2 / 2 / 2;
    height: 5rem;
    cursor: pointer;
}
.i4{
    grid-area: 2 / 3 / 2 / 3;
    height: 5rem;
    cursor: pointer;
}
.i14{
    grid-area: 2 / 4 / 2 / 4;
    height: 5rem;
    cursor: pointer;
}
.i5{
    grid-area: 3 / 1 / 3 / 1;
    height: 5rem;
    cursor: pointer;
}
.i6{
    grid-area: 3 / 2 / 3 / 2;
    height: 5rem;
    cursor: pointer;
}
.i7{
    grid-area: 3 / 3 / 3 / 3;
    height: 5rem;
    cursor: pointer;
}
.i15{
    grid-area: 3 / 4 / 3 / 4;
    height: 5rem;
    cursor: pointer;
}
.i8{
    grid-area: 4 / 1 / 4 / 1;
    height: 5rem;
    cursor: pointer;
}
.i9{
    grid-area: 4 / 2 / 4 / 2;
    height: 5rem;
    cursor: pointer;
}
.i10{
    grid-area: 4 / 3 / 4 / 3;
    height: 5rem;
    cursor: pointer;
}
.i16{
    grid-area: 4 / 4 / 4 / 4;
    height: 5rem;
    cursor: pointer;
}
.i11{
    grid-area: 5 / 1 / 5 / 1;
    height: 5rem;
    cursor: pointer;
}
.i12{
    grid-area: 5 / 2 / 5 / 2;
    height: 5rem;
    cursor: pointer;
}
.i13{
    grid-area: 5 / 3 / 5 / 3;
    height: 5rem;
    cursor: pointer;
}
.i17{
    grid-area: 5 / 4 / 5 / 4;
    height: 5rem;
    cursor: pointer;
}
.i18{
    grid-area: 6 / 1 / 6 / 5;
    height: 5rem;
    cursor: pointer;
}

@media screen and (max-width:1050px) {
        .calc{
            width: 60%;
            height: 100%;
        }
}
@media screen and (max-width:550px) {
    .calc{
        width: 100%;
        height: 100%;
    }
}